@import '../../scss/variables';
@import '../../scss/mixins';


/* portfolio */
.hover-wrapper {
    overflow: hidden;
  
    img {
      transition: .3s ease;
      transform: scale(1.1);
    }
  
    &:hover {
      img {
        transform: scale(1);
      }
  
      .hover-overlay {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  
  .hover-overlay {
    position: absolute;
    height: 100%;
    width: 100%;
    border-radius: inherit;
    top: 0;
    left: 0;
    background: rgba($color: $black, $alpha: .3);
    opacity: 0;
    visibility: hidden;
    transition: .3s ease;
  }
  
  .hover-content {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    right: 0;
    text-align: center;
  }
  
  /* /portfolio */
#portfolio_section{
  background: rgba(224, 241, 255, 0.54);
}